.awesome-lightsaber-wrapper {
    text-align: center;
    height: 100%;
}

@-webkit-keyframes flicker {
    0% {opacity: 1;}
    50% {opacity: 0.85;}
    100% {opacity: 1;}
}

@keyframes flicker {
    0% {opacity: 1;}
    50% {opacity: 0.85;}
    100% {opacity: 1;}
}

.awesome-lightsaber {
    height: 100%;
    width: auto;
    -webkit-transform-origin: center bottom;
        -ms-transform-origin: center bottom;
            transform-origin: center bottom;
}

.awesome-lightsaber-img {
    height: 105%;
    width: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-animation: flicker 150ms linear infinite;
            animation: flicker 150ms linear infinite;
    -webkit-transform: scale(1.38, 1);
        -ms-transform: scale(1.38, 1);
            transform: scale(1.38, 1);
    -o-object-fit: contain;
       object-fit: contain;
}


